// ** Reactstrap Imports
import { Row, Col, Form, Button, Modal, Input, Label, ModalBody, ModalHeader } from 'reactstrap'
import { useForm, Controller } from 'react-hook-form'
import { Check,  X } from 'react-feather'
import { selectThemeColors } from '@utils'
import { useEffect, useState } from 'react'
import { useParams, useHistory } from 'react-router-dom'
import {useDispatch, useSelector } from 'react-redux'
import Select from 'react-select'
import { toast, Slide } from 'react-toastify'
import { getSingleCustomer, updateCustomer } from './store/customerSlice'
const ToastContent = ({ name}) => (
  <>
    <div className='toastify-header'>
      <div className='title-wrapper'>
        <h6 className='toast-title fw-bold'>You have successfully updated Customer {name.toUpperCase()}</h6>
      </div>
    </div>
  </>
)

const Edit = () => {
    // ** State
    const id = useParams()
    const dispatch = useDispatch()
    const store = useSelector(state => state.customers)
    const [selectedStatus, setSelectedStatus] = useState(false)
    const history = useHistory()
      // ** Hook
    const {
    reset,
    control,
    setValue,
    setError,
    handleSubmit,
    formState: { errors }
    } = useForm({
    defaultValues: {
      name: store.message.name,
      email: store.message.email,
      contactNumber: store.message.contactNumber,
      address: store.message.address
  }
    })
    const onSubmit = data => {
      console.log(Object.values(data))
      if (Object.values(data).every(field => field.length > 0)) {
        console.log(data)
        dispatch(updateCustomer({
          ...store.message,
          ...data, 
          status : selectedStatus.value
        })).then(res => {
          history.push("/user/customer")
          toast.success(
            <ToastContent name={res.payload.name || res.payload.username || 'John Doe'} />,
            { icon: false, transition: Slide, hideProgressBar: true, autoClose: 2000 }
          )
        })
      } else {
        for (const key in data) {
          if (data[key].length === 0) {
            setError(key, {
              type: 'manual'
            })
          }
        }
      }
    }
    const statusOptions = [
      { value: true, label: 'Active' },
      { value: false, label: 'Inactive' }
    ]
    const handleReset = () => {
        reset({
            name: store.message.name,
            email: store.message.email,
            contactNumber: store.message.contactNumber,
            address: store.message.address
        })
      }

      useEffect(() => {
        dispatch(getSingleCustomer(id.id)).then((res) => {
          setValue('name', res.payload.name) 
          setValue('email', res.payload.email) 
          setValue('contactNumber', res.payload.contactNumber) 
          setValue('address', res.payload.address) 
          setSelectedStatus(res.payload.status)  
        })
      }, [dispatch, setValue, id.id])

    return ( 
        <>
       <div className='text-center mb-2'>
            <h1 className='mb-1'>Edit Customer Information</h1>
            <p>Updating Customer details will receive a privacy audit.</p>
          </div>
          <Form onSubmit={handleSubmit(onSubmit)}>
            <Row className='gy-1 pt-75'>
              <Col md={6} xs={12}>
                <Label className='form-label' for='name'>
                  Name
                </Label>
                <Controller
                  control={control}
                  type='name'
                  id='name'
                  name='name'
                  render={({ field }) => (
                    <Input {...field} id='name' placeholder='Doe' invalid={errors.name && true} value={field.value || ''} />
                  )}
                />
              </Col>
              <Col md={6} xs={12}>
                <Label className='form-label' for='email'>
                  Email
                </Label>
                <Controller
                  control={control}
                  type='email'
                  id='email'
                  name='email'
                  render={({ field }) => (
                    <Input {...field} id='email' placeholder='Doe' invalid={errors.email && true} value={field.value || ''} />
                  )}
                />
              </Col>
              <Col xs={12}>
                <Label className='form-label' for='address'>
                  Address
                </Label>
                <Controller
                  control={control}
                  id='address'
                  name='address'
                  render={({ field }) => (
                    <Input {...field} id='address' placeholder='john.doe.007' invalid={errors.address && true} value={field.value || ''}/>
                  )}
                />
              </Col>
              <Col xs={6}>
                <Label className='form-label' for='contactNumber'>
                  Contact
                </Label>
                <Controller
                  control={control}
                  id='contactNumber'
                  name='contactNumber'
                  render={({ field }) => (
                    <Input {...field} id='contactNumber' placeholder='john.doe.007' invalid={errors.contactNumber && true} value={field.value || ''}/>
                  )}
                />
              </Col>
              <Col md={6} xs={12}>
                <Label className='form-label' for='status'>
                  Status:
                </Label>
                <Select
                  id='status'
                  isClearable={false}
                  className='react-select'
                  classNamePrefix='select'
                  options={statusOptions}
                  theme={selectThemeColors}
                  value={statusOptions[statusOptions.findIndex(i => i.value === selectedStatus)]}
                  onChange={option => setSelectedStatus(option)}
                />
              </Col>
              <Col xs={12}>
                <div className='d-flex align-items-center mt-1'>
                  <div className='form-switch'>
                    <Input type='switch' defaultChecked id='billing-switch' name='billing-switch' />
                    <Label className='form-check-label' htmlFor='billing-switch'>
                      <span className='switch-icon-left'>
                        <Check size={14} />
                      </span>
                      <span className='switch-icon-right'>
                        <X size={14} />
                      </span>
                    </Label>
                  </div>
                  <Label className='form-check-label fw-bolder' for='billing-switch'>
                    Use as a billing address?
                  </Label>
                </div>
              </Col>
              <Col xs={12} className='text-center mt-2 pt-50'>
                <Button type='submit' className='me-1' color='primary'>
                  Submit
                </Button>
                <Button
                  type='reset'
                  color='secondary'
                  outline
                  onClick={() => {
                    handleReset()
                  }}
                >
                  Discard
                </Button>
              </Col>
            </Row>
          </Form>
        </>
     )
}
 
export default Edit